.container {
    padding: 100px 5%;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1 {
        font-size: 18px;
        font-weight: 600;
        line-height: 32px;
    }
    p {
        font-size: 16px;
        font-weight: 400;
        text-align: justify;
    }

    .heading {
        margin: 20px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        h1 {
            margin: 20px 0;
        }
    }

    .sub-container {
        margin: 20px 0;

        li {
            text-align: justify;
        }
        ul {
            list-style: inside;
        }
    
        ol {
            list-style-type:decimal;
        }
    
        ul, ol {
            padding-left: 20px;
        }
    }

   

    @media (min-width: 700px){
        padding: 0 25% ;

        .sub-container {
            ul, ol {
                padding-left: 30px;
            }
        }
    }
}